/// <reference lib="webworker" />
/* eslint-disable no-restricted-globals */

// This service worker can be customized!
// See https://developers.google.com/web/tools/workbox/modules
// for the list of available Workbox modules, or add any other
// code you'd like.
// You can also remove this file if you'd prefer not to use a
// service worker, and the Workbox build step will be skipped.

import { clientsClaim } from 'workbox-core';
import { ExpirationPlugin } from 'workbox-expiration';
import { precacheAndRoute, createHandlerBoundToURL } from 'workbox-precaching';
import { registerRoute } from 'workbox-routing';
import { StaleWhileRevalidate } from 'workbox-strategies';
import {CheckForTheCenters} from "./main/cowin/components/vaccinesessions/CowinRequestManager";
import ReactGA from "react-ga";
import config from "./main/cowin/config.json";

declare const self: ServiceWorkerGlobalScope;
let is_running: boolean;
is_running = false;

ReactGA.initialize(config.GOOGLE_ANALYTICS_UA);
clientsClaim();

// Precache all of the assets generated by your build process.
// Their URLs are injected into the manifest variable below.
// This variable must be present somewhere in your service worker file,
// even if you decide not to use precaching. See https://cra.link/PWA
precacheAndRoute(self.__WB_MANIFEST);

// Set up App Shell-style routing, so that all navigation requests
// are fulfilled with your index.html shell. Learn more at
// https://developers.google.com/web/fundamentals/architecture/app-shell
const fileExtensionRegexp = new RegExp('/[^/?]+\\.[^/]+$');
registerRoute(
    // Return false to exempt requests from being fulfilled by index.html.
    ({ request, url }: { request: Request; url: URL }) => {
        // If this isn't a navigation, skip.
        if (request.mode !== 'navigate') {
            return false;
        }

        // If this is a URL that starts with /_, skip.
        if (url.pathname.startsWith('/_')) {
            return false;
        }

        // If this looks like a URL for a resource, because it contains
        // a file extension, skip.
        if (url.pathname.match(fileExtensionRegexp)) {
            return false;
        }

        // Return true to signal that we want to use the handler.
        return true;
    },
    createHandlerBoundToURL(process.env.PUBLIC_URL + '/index.html')
);

// An example runtime caching route for requests that aren't handled by the
// precache, in this case same-origin .png requests like those from in public/
registerRoute(
    // Add in any other file extensions or routing criteria as needed.
    ({ url }) => url.origin === self.location.origin && url.pathname.endsWith('.png'),
    // Customize this strategy as needed, e.g., by changing to CacheFirst.
    new StaleWhileRevalidate({
        cacheName: 'images',
        plugins: [
            // Ensure that once this runtime cache reaches a maximum size the
            // least-recently used images are removed.
            new ExpirationPlugin({ maxEntries: 50 }),
        ],
    })
);

// This allows the web app to trigger skipWaiting via
// registration.waiting.postMessage({type: 'SKIP_WAITING'})
self.addEventListener('message', (event) => {
    console.log('message', event)
    if (event.data && event.data.type === 'SKIP_WAITING') {
        self.skipWaiting();
    }
});

function openPushNotification(event: any) {
    event.notification.close();
    event.waitUntil(self.clients.openWindow(event.notification.data));
}

self.addEventListener("notificationclick", openPushNotification);

function ShowNotification(body:any) {
    const options = {
        data: "https://selfregistration.cowin.gov.in/",
        body: body,
        icon: "https://www.bender-latinamerica.com/fileadmin/content/BenderGroup/Img/news/general/2020/Icon_Virus.jpg",
        vibrate: [200, 100, 200],
        duration: 10000,
        image: "https://www.bender-latinamerica.com/fileadmin/content/BenderGroup/Img/news/general/2020/Icon_Virus.jpg",
        badge: "https://spyna.it/icons/favicon.ico",
        requireInteraction: true,
        tag: "Cowin-notification"
    };
    self.registration.showNotification("Slot Alert", options);
}

function myFunction(){
    try {
        if(self.navigator.onLine) {
            readDatabaseAndCreateRequests();
        }
    } catch (e) {
       console.error(e)
    }
}

self.addEventListener('activate', (event) => {
    console.log("ACTIVATE");

    if (Notification.permission === 'granted' && !is_running) {
        is_running = true;
        setTimeout(() => {ShowNotification("Welcome, We'll let you know once slot will available.")}, 6500);
        event.waitUntil(setInterval(myFunction, 300000));
    }
});

self.addEventListener('fetch', () => {
    console.log("F");
    if (Notification.permission === 'granted' && !is_running) {
        is_running = true;
        setInterval(myFunction, 300000);
    }
})


function readDatabaseAndCreateRequests() {
    let openDBRequest = self.indexedDB.open('cowin', config.DB_VERSION);
    openDBRequest.onupgradeneeded = function (event){
        if(event.oldVersion) {
            let database = openDBRequest.result;
            if(database.objectStoreNames.contains("centers")){
                database.deleteObjectStore("centers");
                let store = database.createObjectStore("centers", {autoIncrement: true});
                store.createIndex('pin_code', 'pin_code', {unique: true});
            }
        }
    }

    openDBRequest.onsuccess = function (){
        let database = openDBRequest.result;
        if(database.objectStoreNames.contains('centers')) {
            let transaction = database.transaction(['centers'], 'readonly');
            let results = transaction.objectStore('centers').getAll();
            results.onsuccess = ev => {
                results.result.forEach(t_center => {
                    CheckForTheCenters(t_center.pin_code, CallbackFunction);
                })
            }
        }
    };
}

function CallbackFunction(centers: any){
    if(centers && centers.length > 0) {
        centers.forEach( (c:any) =>{
            c.sessions.forEach( (s:any) => {
                if(s.min_age_limit === 18 && s.available_capacity > 0){
                    return ShowNotification("" + s.available_capacity + " cowin vaccines are available for " + c.name);
                }
            })
        });
    }
}

// Any other custom service worker logic can go here.
